"use client";

import React, { useState, useEffect } from 'react';
// @ts-ignore
import Map, { Marker } from 'react-map-gl';
import 'mapbox-gl/dist/mapbox-gl.css';

// Define what a "Point" looks like so TypeScript is happy
interface Point {
  latitude: number;
  longitude: number;
  busyness: number; // 1-10
}

const GANGNAM_STATION = {
  lat: 37.498095,
  lng: 127.027610
};

export default function CrowdRadar() {
  const [points, setPoints] = useState<Point[]>([]);

  // Generate fake data on startup
  useEffect(() => {
    const newPoints: Point[] = [];
    for (let i = 0; i < 20; i++) {
      // Create random offset for 500m radius
      const angle = Math.random() * Math.PI * 2;
      const radius = Math.random() * 0.0045; // Approx 500m in degrees

      newPoints.push({
        latitude: GANGNAM_STATION.lat + Math.sin(angle) * radius,
        longitude: GANGNAM_STATION.lng + Math.cos(angle) * radius,
        busyness: Math.floor(Math.random() * 10) + 1,
      });
    }
    setPoints(newPoints);
  }, []);

  // Helper to pick color based on busyness
  const getColor = (level: number) => {
    if (level >= 8) return '#ef4444'; // Red (Busy)
    if (level >= 4) return '#eab308'; // Yellow (Moderate)
    return '#22c55e'; // Green (Quiet)
  };

  return (
    <div className="w-full h-screen bg-black">
      <Map
        mapboxAccessToken={process.env.NEXT_PUBLIC_MAPBOX_TOKEN}
        initialViewState={{
          longitude: GANGNAM_STATION.lng,
          latitude: GANGNAM_STATION.lat,
          zoom: 15
        }}
        style={{ width: '100%', height: '100%' }}
        mapStyle="mapbox://styles/mapbox/dark-v11"
      >
        {/* Render the Radar Dots */}
        {points.map((p, index) => (
          <Marker 
            key={index} 
            longitude={p.longitude} 
            latitude={p.latitude}
          >
            <div 
              style={{
                width: '12px',
                height: '12px',
                borderRadius: '50%',
                backgroundColor: getColor(p.busyness),
                boxShadow: `0 0 10px ${getColor(p.busyness)}`, // Neon Glow
                border: '2px solid white'
              }}
            />
          </Marker>
        ))}
      </Map>
    </div>
  );
}
